    
<template>
  <v-content class="ma-0 pa-0">
  <v-sheet color="blue lighten-5 px-4 py-9">
    <h2> Welcome back "{{ $store.state.auth.user.displayName }}"</h2>
  </v-sheet>
  <v-row>
    <v-col><app-panel title="Today" :count="total.daily.count" :amount="total.daily.amount" /></v-col>
    <v-col><app-panel title="This month" :count="total.monthly.count" :amount="total.monthly.amount" /></v-col>
  </v-row>
 <v-row>
    <v-col>
      <v-toolbar flat>
        <v-toolbar-title>
          Today deposits
        </v-toolbar-title>
        <v-spacer></v-spacer>
        
        <v-btn  color="green white--text mx-2" v-if="saleWrite" @click="$router.push('/sale/new')"> <v-icon>mdi-plus</v-icon> New sale deposit</v-btn>
        <v-btn  color="green white--text mx-2" v-if="retentionWrite" @click="$router.push('/retention/new')"> <v-icon>mdi-plus</v-icon>New retention deposit</v-btn>
      
      
      </v-toolbar>
    <v-data-table :headers="headers" :items="items"  @click:row="goTo('today', $event)"> 
     <template v-slot:item.created_on="{ item }" >
        {{ item.created_on.split('T')[0] }} 
      </template> 
      <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
      </template>
    </v-data-table></v-col>
  </v-row>
  
  <v-row>
    <v-col>
      <v-toolbar flat>
        <v-toolbar-title>
          This month deposits
        </v-toolbar-title>
      </v-toolbar>
    <v-data-table :headers="mheaders" :items="mitems" @click:row="goTo('month', $event)"> 
     <template v-slot:item._id="{ item }" >
        {{ item._id.split('T')[0] }} 
      </template> 
      <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
      </template>
    </v-data-table></v-col>
  </v-row>
 
  </v-content>

</template>
<script>
import { mapActions } from 'vuex'
import AppPanel from './../components/dashboard/Panel'
import collections from './../collections'
import $ from 'jquery' 

export default {
  name: 'UserDashboard',
  components: {
    AppPanel
  },
  data () {
    return {
      headers: [],
      items: [],
      mheaders: [
        {
          text: 'Date',
          align: 'start',
          value: '_id',
          width: '30%'
        },
        { text: 'Users', value: 'count', width: '30%' },
        { text: 'Total', value: 'amount' }
      ],
      mitems: [],
      total: {
        daily: {
          amount: 0,
          count: 0
        },
        monthly: {
          amount: 0,
          count: 0
        }
      },
      dialog: {
        value: false,
        title: '',
        collection: 'sale'
      }
    }
  },
  computed: {
    retentionRead () {
      const coll = this.access('retention')
      if (coll[0])
        return coll[0].read
      return false
    },
    saleRead () {
      const coll = this.access('sale')
      if (coll[0])
        return coll[0].read
      return false
    },
    retentionWrite () {
      const coll = this.access('retention')
      if (coll[0])
        return coll[0].write
      return false
    },
    saleWrite () {
      const coll = this.access('sale')
      if (coll[0])
        return coll[0].write
      return false
    }
    
  },
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
    goTo (type, item) {
      if (type == 'today') {
        this.$router.push('/'+ item.type + '/' + item._id)
      }
      if (type == 'month') {
        const query = {
          filterBy: [ 'user', 'created_on' ],
          filterValue:[this.$store.state.auth.user._id, item._id],
          filterType:['normal', 'normal'],
          filterCast:['id', 'date'],
          sortBy: ['created_on'],
          sortDesc: [false]
        }
        const coll = this.retentionRead ? 'retention' : (this.saleRead ? 'sale' : false)
        if (coll)
          this.$router.push('/' + coll + '?' + $.param(query))

      }
       
    },
    writeAccess (collection) {
      const access = this.access(collection)
      if (access.length > 0)
        return access[0].write

      return false 
    },
    access (collection) {
        if (this.$store.state.auth.user.rights) {
          return this.$store.state.auth.user.rights.filter(e => {
              return e.value ==  collection  
          })
        } 
        return []
      },
    openDialog (collection) {

      this.dialog.title = collection
      this.dialog.collection = collection
      this.dialog.value = true
    },
    newSave () {
      this.dialog.value = false
    }
  },
  async created () {

    let today = new Date();
    today.setHours(0, 0, 0, 0);
    const first_day_of_the_month = new Date(today.getFullYear(), today.getMonth(), 1)


    const totalToday = await this.get({ collection: 'dashboard/total', query: {
      query: { 
        user: { type: 'normal', value: this.$store.state.auth.user._id, cast: 'id' },
        created_on : { type: 'gte', value: today.getTime() , cast: 'date' }
      }
    } }) 
    const totalThisMonth = await this.get({ collection: 'dashboard/total', query: {
      query: { 
        user: { type: 'normal', value: this.$store.state.auth.user._id, cast: 'id' },
        created_on : { type: 'gte', value: first_day_of_the_month.getTime() , cast: 'date' }
      }
    } })
    
    if (totalToday && totalToday.data ) {
      this.total.daily.amount = totalToday.data.length ? totalToday.data[0].amount : 0
      this.total.daily.count = totalToday.data.length ? totalToday.data[0].count : 0
    }
    
    if (totalThisMonth && totalThisMonth.data ) {
      this.total.monthly.amount = totalThisMonth.data.length ? totalThisMonth.data[0].amount : 0
      this.total.monthly.count = totalThisMonth.data.length ? totalThisMonth.data[0].count : 0
    }
    
     
    const collection = collections.get( 'sale')
    this.headers = Object.assign([],collection.table)
    this.headers = this.headers.filter(e => ["actions", "business_unit", "user.displayName"].indexOf(e.value) === -1)
  
     let d = new Date();
    d.setHours(0, 0, 0, 0);
    const result = await this.get({ collection: 'deposit', query: {
      query: {user: {type: "normal", value: this.$store.state.auth.user._id}, created_on: {type: "gte", value:d}}, 
      limit: 10
    } })
    if (result && result.data)
      this.items = result.data.docs




    const mresult = await this.get({ collection: 'dashboard/user_monthly_deals'})
    if (mresult && mresult.data)
      this.mitems = mresult.data 
 



  }
}
</script>